// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header';
import muiUtils from 'common/muiUtils';
import home from '../bizcomponent/Home';
import allgoods from '../bizcomponent/AllGoods';
import activitygoods from '../bizcomponent/ActivityGoods';

Vue.use(VueRouter);
var router = new VueRouter();

router.map({
    '/': {
        component: home
    },
    '/home': {
        component: home
    },
    '/allgoods': {
        component: allgoods
    },
    '/activitygoods': {
        component: activitygoods
    }
});

router.redirect({
    '*': '/home'
});

//mui生命周期管理
mui.init({
    swipeBack: true
});

//头部head start
var HeaderVue = Vue.extend(vheader);
var header = new HeaderVue({
    propsData: {
        hdstyle: {
            back: {
                display: true
            },
            searchBar: {
                display: true,
                placeholder: '搜索店铺内商品',
                modelName: 'store',
                gotoPage:'../../buyer/homesearch/index.html',
                extras: {
                    store_id: ''
                }
            },
            rightButton: {
                display: true,
                text: '首页',
                onClick: function() {
                    muiUtils.openIndexWindow('buyer');
                }
            },
        },
        typeclass: 'wantBuy',
        currentSearchType: 'goods'
    },
    el: 'vheader'
});
//头部head end

//尾部foot start
var FootVue = Vue.extend({});
var footer = new FootVue({
    el: '.bottom-button',
    methods: {
        displayType: function() {
            router.app.displayType();
        },
        gotoStoreInfo: function() {
            router.app.gotoStoreInfo();
        }
    }
});
//尾部 end

mui.plusReady(function() {
    header.hdstyle.searchBar.extras.store_id = plus.webview.currentWebview().store_id;
    var fromSeller = plus.webview.currentWebview().fromSeller;
    if(fromSeller) {
        header.hdstyle.rightButton.onClick = () => {
            muiUtils.openIndexWindow('seller');
        }
    }
    router.start(app, '#app');
});